<!-- newPizza -->
<template>
	<form>
		<h3 class="text-muted my-5">添加新的Pizza</h3>
		<div class="form-group row">
			<label for="">品种</label>
			<input type="text" class="form-control" v-model="newPizza.name">
		</div>
		<div class="form-group row">
			<label for="">描述</label>
			<textarea rows="5" class="form-control" v-model="newPizza.description"></textarea>
		</div>
		<p><strong>选项一</strong></p>
		<div class="form-group row">
			<label for="">尺寸</label>
			<input type="text" class="form-control" v-model="newPizza.size1">
		</div>
		<div class="form-group row">
			<label for="">价格</label>
			<input type="text" class="form-control" v-model="newPizza.price1">
		</div>
		<p><strong>选项二</strong></p>
		<div class="form-group row">
			<label for="">尺寸</label>
			<input type="text" class="form-control" v-model="newPizza.size2">
		</div>
		<div class="form-group row">
			<label for="">价格</label>
			<input type="text" class="form-control" v-model="newPizza.price2">
		</div>
		<div class="form-group row">
			<button @click.prevent="addMenuItem" class="btn btn-block btn-success">添加</button>
		</div>
	</form>
</template>

<script>
	export default {
		data(){
	  		return{
	  			newPizza:{}
	  		}
		},
		methods:{
			addMenuItem(){
				let data = {
					name:this.newPizza.name,
					desctiption:this.newPizza.desctiption,
					options:[
						{
							size:this.newPizza.size1,
							price:this.newPizza.price1
						},
						{
							size:this.newPizza.size2,
							price:this.newPizza.price2
						}
					]
				}
				this.$store.commit("addMenuItems",data)
				fetch("https://point-meal-d67e8.firebaseio.com/Menu.json",{
					method:"POST",
					headers:{
						"Content-type":"application/json"
					},
					body:JSON.stringify(data)
				})
				.then(res =>res.json())
				.then(data =>{
					// this.$router.push("/Menu")
					// console.log(data)
				})
			}
		}
	}
</script>

<style scoped>
	
</style>



