<!-- 管理 -->
<template>
	<div class="row">
		<div class="col-sm-12 col-md-8">
			<!-- new pizza -->
			<NewPizza></NewPizza>
		</div>
		<div class="col-sm-12 col-md-4">
			<!-- 品种展示 -->
			<h3 class="text-muted my-5">菜单</h3>
			<table class="table">
				<thead class="table table-default">
					<tr>
						<th>品种</th>
						<th>删除</th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="item in getMenuArray" :key="item.name">
						<td>{{item.name}}</td>
						<td><button @click="delData(item)" class="btn btn-sm btn-outline-success">&times;</button></td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</template>

<script>
	import NewPizza from './NewPizza'
	export default {
		data(){
	  		return{
	  			// getMenuArray:""
	  		}
		},
		components:{
			NewPizza
		},
		computed:{
			getMenuArray:{
				//在vuex中获取属性
				get(){
					return this.$store.getters.getMenuItems;
				},
				set(){
					
				}
			}
		},
		created(){
			fetch("https://point-meal-d67e8.firebaseio.com/Menu.json")
			.then(res =>{
				return res.json()
			})
			.then(data=>{
				let MenuArray = [];
				for(let item in data){
					data[item].id=item;
					MenuArray.push(data[item])
				}
				// this.getMenuArray=MenuArray;
				this.$store.commit("setMenuItems",MenuArray)
			})
		},
		methods:{
			delData(item){
				fetch("https://point-meal-d67e8.firebaseio.com/Menu/" + item.id + "/.json",{
					method:'DELETE',
					headers:{
						"Content-type":"application/json"
					}
				})
				.then(res=>res.json())
				.then(data=>{
					// this.$router.push("/Menu")
					this.$store.commit("delMenuItems",item)
				})
			}
		}
	}
</script>

<style scoped>

</style>



