<!-- 导航 -->
<template>
	<nav class="site-header sticky-top py-1">
		<div class="container d-flex flex-column flex-md-row justify-content-between">
			<router-link to="/" class="navbar-brand d-flex align-items-center">
				<router-link class="py-2" to="/">
					<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor"
					 stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="d-block mx-auto">
						<circle cx="12" cy="12" r="10"></circle>
						<line x1="14.31" y1="8" x2="20.05" y2="17.94"></line>
						<line x1="9.69" y1="8" x2="21.17" y2="8"></line>
						<line x1="7.38" y1="12" x2="13.12" y2="2.06"></line>
						<line x1="9.69" y1="16" x2="3.95" y2="6.06"></line>
						<line x1="14.31" y1="16" x2="2.83" y2="16"></line>
						<line x1="16.62" y1="12" x2="10.88" y2="21.94"></line>
					</svg>
				</router-link>
				<router-link to="/">Pizza点餐系统</router-link>
			</router-link>
			<ul class="nav align-items-center">
				<li class="nav-item pad">
					<router-link to="/Point-meal/" class="py-2 d-none d-md-inline-block">主页</router-link>
				</li>
				<li class="nav-item pad">
					<router-link to="/Point-meal/Menu" class="py-2 d-none d-md-inline-block">菜单</router-link>
				</li>
				<li class="nav-item pad">
					<router-link to="/Point-meal/Admin" class="py-2 d-none d-md-inline-block">管理</router-link>
				</li>
				<li class="nav-item pad">
					<router-link to="/Point-meal/About" class="py-2 d-none d-md-inline-block">关于我们</router-link>
				</li>
			</ul>
			<ul class="nav align-items-center navbar-right">
				<li class="nav-item">
					<router-link to="/Point-meal/Login" v-show="!isLogin" class="nav-link">登录</router-link>
				</li>
				<li class="nav-item">
					<a class="nav-link">{{currentUser}}</a>
				</li>
				<li class="nav-item">
					<router-link to="/Point-meal/Login" v-show="isLogin" class="nav-link">[退出]</router-link>
				</li>
				<li class="nav-item ml-1">
					<router-link to="/Point-meal/Register" v-show="!isLogin" class="nav-link">注册</router-link>
				</li>
			</ul>
		</div>
	</nav>
</template>

<script>
	export default {
		computed:{
			currentUser(){
				return this.$store.getters.currentUser;
			},
			isLogin(){
				return this.$store.getters.isLogin;
			}
		}
	}
</script>

<style scoped>
	.site-header {
		background-color: rgba(0, 0, 0, .85);
	}

	.sticky-top {
		position: -webkit-sticky;
		position: sticky;
		top: 0;
		z-index: 1020;
	}

	.site-header a {
		color: #999;
		transition: ease-in-out color .15s;
	}

	a {
		color: #007bff;
		text-decoration: none;
		background-color: transparent;
	}

	a:hover {
		color: #ffffff;
	}

	.container {
		max-width: 960px;
	}

	.pad {
		padding: 0px 35px;
	}
</style>



