<!-- 主页 -->
<template>
	<div class="row">
		<div class="col-sm-12 text-center" id="home">
			<div class="background">
				<h1>欢迎大家品尝pizza</h1>
				<h2>这里有你非常喜欢的pizza</h2>
				<button @click="goToMenu" class="btn btn-success">到菜单</button>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		methods:{
			goToMenu(){
				//跳转到上一次浏览的页面
				//this.$router.go(-1)
				//指定跳转的地址
				//this.$router.replace('/Menu')
				//跳转到指定路由的名字下
				//this.$router.replace({name:'Menu'})
				//通过push进行跳转
				this.$router.push('/Point-meal/Menu')
			}
		}
	}
</script>

<style scoped>
	#home{
		background: url(../assets/img/pizza.jpg) 0 0 no-repeat;
		background-size: 1212px 755px;
		height: 85vh;
		padding:10%;
	}
	h1,h2{
		margin: 6%;
	}
	.background{
		background: #eee;
		opacity: 0.8;
		max-width: 70vw;
		margin:0 auto;
		padding: 20px 0;
	}
</style>



