<!-- 注册 -->
<template>
	<div class="row mt-3">
		<div class="col-md-12 col-lg-12">
			<div class="card">
				<div class="card-body">
					<form @submit.prevent="onSub">
						<div class="from-group container">
							<div class="img-pizzaIcon"></div>
						</div>
						<div class="from-group">
							<label for="email">邮箱</label>
							<input 
								type="email"
								class="form-control"
								v-model="email"
								>
						</div>
						<div class="from-group">
							<label for="password">密码</label>
							<input 
								type="password"
								class="form-control"
								v-model="password"
								>
						</div>
						<div class="from-group">
							<label for="confirm-password">确认密码</label>
							<input 
								type="confirm-password"
								class="form-control"
								v-model="confirmPassword"
								>
						</div>
						<button type="submit" class="btn btn-block btn-success">注册</button>
					</form>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data() {
			return {
				email: "",
				password:"",
				confirmPassword:""
			}
		},
		methods: {
			onSub() {
				if(this.password===this.confirmPassword){
					const formData={
						email:this.email,
						password:this.password,
						confirmPassword:this.confirmPassword
					}
					this.$axios
						.post("https://point-meal-d67e8.firebaseio.com/users.json",formData)
						.then(res=>{
							console.log(formData)
						})
				}
			}
		}
	}
</script>

<style scoped>
	.container{
		text-align: center;
	}
	.img-pizzaIcon{
		width: 200px;
		height: 200px;
		background: url(../assets/img/pizzaIcon.png) center center no-repeat;
		background-size: 200px 200px;
		margin: 0 auto;
	}
</style>



