<!-- 登录 -->
<template>
	<div class="row mt-3">
		<div class="col-md-12 col-lg-12">
			<div class="card">
				<div class="card-body">
					<form @submit.prevent="onSub">
						<div class="from-group container">
							<div class="img-pizzaIcon"></div>
						</div>
						<div class="from-group">
							<label for="email">邮箱</label>
							<input 
								type="email"
								class="form-control"
								v-model="email"
								>
						</div>
						<div class="from-group">
							<label for="password">密码</label>
							<input 
								type="password"
								class="form-control"
								v-model="password"
								>
						</div>
						<button type="submit" class="btn btn-block btn-success">登录</button>
					</form>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data() {
			return {
				email: "",
				password:""
			}
		},
		beforeRouteEnter:(to,from,next)=>{
			next(vm => vm.$store.dispatch("setUser",null))
		},
		methods: {
			onSub() {
				this.$axios
					.get("https://point-meal-d67e8.firebaseio.com/users.json")
					.then(res=>{
						const data=res.data;
						const users=[];
						for(let key in data){
							const user = data[key]
							users.push(user)
						}
						let result=users.filter((user)=>{
							return user.email===this.email&&user.password===this.password
						})
						if(result!=null&&result.length>0){
							this.$store.dispatch("setUser",result[0].email)
							this.$router.push({path:"/Point-meal/"});
						}else{
							alert("账号或密码错误")
							this.$store.dispatch("setUser",null)
						}
					})
			}
		}
	}
</script>

<style scoped>
	.container{
		text-align: center;
	}
	.img-pizzaIcon{
		width: 200px;
		height: 200px;
		background: url(../assets/img/pizzaIcon.png) center center no-repeat;
		background-size: 200px 200px;
		margin: 0 auto;
	}
</style>



