import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Menu from '@/components/Menu'
import Admin from '@/components/Admin'
import About from '@/components/about/About'
import Login from '@/components/Login'
import Register from '@/components/Register'
//二级路由
import History from '@/components/about/History'
import Contact from '@/components/about/Contact'
import OrderingGuide from '@/components/about/OrderingGuide'
import Delivery from '@/components/about/Delivery'
//三级路由
import Phone from '@/components/about/Contact/Phone'
import PersonName from '@/components/about/Contact/PersonName'
Vue.use(Router)

export default new Router({
	mode:"history",
	//滚动行为
	/* scrollBehavior (to, from, savedPosition) {
    return {selector:'.btn'}
  }, */
  routes: [
    {
      path: '/Point-meal/',
      name: 'Home',
			//路由复用
      components: {
				default:Home,
				"OrderingGuide":OrderingGuide,
				"Delivery":Delivery,
				"History":History
			}
    },
		{
		  path: '/Point-meal/Menu',
		  name: 'Menu',
		  component: Menu
		},
		{
		  path: '/Point-meal/Admin',
		  name: 'Admin',
		  component: Admin
		},
		{
		  path: '/Point-meal/About',
		  name: 'About',
		  component: About,
			redirect:'/Point-meal/About/History',
			children:[
				{
				  path: '/Point-meal/About/History',
				  name: 'History',
				  component: History
				},
				{
				  path: '/Point-meal/About/Contact',
				  name: 'Contact',
				  component: Contact,
					children:[
						{
						  path: '/Point-meal/About/Contact/Phone',
						  name: 'Phone',
						  component: Phone
						},
						{
						  path: '/Point-meal/About/Contact/PersonName',
						  name: 'PersonName',
						  component: PersonName
						}
					]
				},
				{
				  path: '/Point-meal/About/OrderingGuide',
				  name: 'OrderingGuide',
				  component: OrderingGuide
				},
				{
				  path: '/Point-meal/About/Delivery',
				  name: 'Delivery',
				  component: Delivery
				}
			]
		},
		{
		  path: '/Point-meal/Login',
		  name: 'Login',
		  component: Login
		},
		{
		  path: '/Point-meal/Register',
		  name: 'Register',
		  component: Register
		}
  ]
})


